// eslint-disable-next-line no-unused-vars
import axios, { AxiosRequestConfig } from 'axios';
import Cookie from "js-cookie";
import cogoToast from 'cogo-toast';
import constantes from '../config/constantes';

/**
 * @param {AxiosRequestConfig} config
*/
export const restAuth = async (config) => {
  const closeLoading = cogoToast.loading('Iniciando sesin...', {
    hideAfter: 0, position: 'top-right', role: "success"
  });

  const newAxiosRequest = axios.create({
    baseURL: constantes.urlApi
  });

  return await newAxiosRequest(({...config})).then(({data}) => {
    closeLoading.hide();
    if (data && data.data) {
      cogoToast.success(data.message, { position: 'top-right' })
      // cogoToast.success('hola',{position: 'top-rigth'})
      return ({data: data.data, errors: false});
    } else {
      cogoToast.error('No se puede obtener la data' ,{ position: 'top-right'})
      throw Error("No se puede obtener la data");
    }
  }).catch(err => {
    closeLoading.hide();
    if (err.response) {
      console.log(err.response);
      const errData = err.response.data;
      cogoToast.warn( errData.message !== undefined ? errData.message : 'Error en la solicitud.',
      { position: 'top-right'})
      return {
        data: false, 
        errors: errData
      }
    } else if (err.request) {
      console.log(err.request);
      cogoToast.warn("Se realiz la solicitud, pero no se recibi respuesta.",
      { position: 'top-right'})
      throw err
    } else {
      cogoToast.error('Algo sucedi al configurar la solicitud que provoc un error.',
      { position: 'top-right'})
      throw err
    }
  })
};

/**
 * @param {AxiosRequestConfig} config
*/
export const restPrivate = async (config) => {
  const closeLoading = cogoToast.loading('Cargando...', {
    hideAfter: 0, position: 'top-right', role: "success"
  });

  const newAxiosRequest = axios.create({
    baseURL: constantes.urlApi,
    headers: {
      Authorization: 'Bearer ' + Cookie.get(constantes.TOKEN_STORAGE_KEY)
    }
  });

  return await newAxiosRequest(({...config})).then(({data}) => {
    closeLoading.hide();
    cogoToast[data.status](data.message, { position: 'top-right' })
    return (data);
  }).catch(err => {
    closeLoading.hide();
    if (err.response) {
      console.log(err.response);
      const errData = err.response.data;
      if (String(err.response.status) === "401") {
        cogoToast.error('Token Invalido, cerrando sesin.',
        { position: 'top-right'});
        Cookie.remove(constantes.TOKEN_STORAGE_KEY);
        document.location.href="/";
      } else {
        cogoToast.warn( errData.message !== undefined ? errData.message : 'Error en la solicitud.',
        { position: 'top-right'})
      }
      return {
        data: {status:'error'}, 
        errors: errData
      }
    } else if (err.request) {
      console.log(err.request);
      cogoToast.warn("Se realiz la solicitud, pero no se recibi respuesta.",
      { position: 'top-right'})
      throw err
    } else {
      cogoToast.error('Algo sucedi al configurar la solicitud que provoc un error.',
      { position: 'top-right'})
      throw err
    }
  })
};