import React, { useState, useEffect } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import { AddCircle } from '@material-ui/icons';

import Titulo from '../../components/common/Titulo';
import TablaCapitulos from '../../components/capitulos/TablaCapitulos';
import { getCapitulosNovela, deleteCapitulo } from '../../services/apiCapitulos';

function ListaCapitulos({match}) {
  const [listaCapitulos, setListaCapitulos] = useState([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(30);
  const [total, setTotal] = useState(1);
  const { id } = match.params;

  useEffect(() => {
    if (id) {
      cargarCapitulos(1, 30, id);
    }
  },[id]);
  //funcion para cargar la lista paginada de capitulos
  async function cargarCapitulos(p, l, n) {
    await getCapitulosNovela(p, l, n).then(({data}) => {
      if (data.docs && (data.docs).length > 0) {
        setListaCapitulos(data.docs);
        setTotal(data.totalDocs);
      } else {
        console.log('sin caps')
      }
    }).catch((error) => console.log(error));
  }
  //funcion para eliminar el capitulo
  async function borrarCapitulo(idCap) {
    if (idCap) {
      return await deleteCapitulo(idCap)
      .then(async () => {
        await cargarCapitulos(Number(page)+1, rowsPerPage, id);
      }) 
    } 
  }
  //funciones para manejar el paginado
  const cambiarPagina = async (event, newPage) => {
    setPage(newPage);
    await cargarCapitulos(Number(newPage)+1, rowsPerPage, id);
  };

  const cambiarLimite = async (event) => {
    let limite = parseInt(event.target.value, 10);
    setRowsPerPage(limite);
    setPage(0);
    await cargarCapitulos(1, limite, id)
  };

  return (
    <div>
      <Titulo>Lista de Capitulos</Titulo>
      <Button 
        component={Link}
        to={`/cms/capitulo/crear/${id}`}
        params={{ id: id }}
        variant="contained" 
        startIcon={<AddCircle/>} 
        style={{marginBottom: '.5rem', backgroundColor: green[500], color: 'white'}}
      >
        Nuevo
      </Button>
      <TablaCapitulos
        listaCapitulos={listaCapitulos}
        total={total}
        rowsPerPage={rowsPerPage}
        page={page}
        cambiarPagina={cambiarPagina}
        cambiarLimite={cambiarLimite}
        borrarCapitulo={borrarCapitulo}
      />
    </div>
  )
};

export default withRouter(ListaCapitulos);
