import React from 'react';
import PropTypes from 'prop-types';
import { Typography, Button } from '@material-ui/core';
import Brightness1TwoToneIcon from '@material-ui/icons/Brightness1TwoTone';
import { green, red } from '@material-ui/core/colors';

function ActivarNovela({novela, activar}) {
  return (
    <div>
      <Typography color="secondary" style={{marginBottom: '1rem'}}>
        En caso de borrar una novela y esta tenga capitulos o imagenes vinculados, entonces se pondr en modo inactivo. Aqui puedes activarla de nuevo.
      </Typography>
      <div style={{margin: 'auto'}}>
        <Brightness1TwoToneIcon style= {{color: novela.activo?green[500]:red[500]}}/>
      </div>
      <Button variant="contained" color="primary" onClick={activar}>
        Activar
      </Button>
    </div>
  )
}

ActivarNovela.propTypes = {
  novela: PropTypes.object,
  activar: PropTypes.func
}

export default ActivarNovela

