import React, { useState } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { 
  Button, TableContainer, Table, 
  TableHead, TableRow, TableCell, 
  TableBody, TableFooter, TablePagination, ButtonGroup 
} from '@material-ui/core';
import { red, green} from '@material-ui/core/colors';
import { Delete, RemoveRedEye } from '@material-ui/icons';

import Dialogo from '../common/Dialogo';
import DialogoImagen from './DialogoImagen';
import { getImage } from '../../services/apiImagenes';

function TablaImagenes({
  listaImagenes=[], 
  total=1, 
  rowsPerPage=30, 
  page=0, 
  cambiarPagina, 
  cambiarLimite,
  borrarImagen}) {
  const [imagenTitulo, setImagenTitulo] = useState('');
  const [idImagen, setIdImagen] = useState(null);
  const [keyImagen, setKeyImagen] = useState(null)
  const [dialogoVisible, setDialogoVisible] = useState(false);
  const [detalleVisible, setDetalleVisible] = useState(false);
  const [loadingImagen, setLoadingImagen] = useState(false);
  const [imagenObj, setImagenObj] = useState({})

  async function eliminar() {
    await borrarImagen(idImagen, keyImagen);
    abrirDialogo();
  }

  async function buscarImagen(idImg) {
    if (idImg !== null && idImg !== undefined) {
      setLoadingImagen(true)
      await getImage(idImg).then(({data}) => {
        setImagenObj(data);
        setLoadingImagen(false);
      }).catch((error) => {
        console.log(error);
        setLoadingImagen(false);
      });
    }
  }
  //funcion para abrir el cuadro de eliminar
  const abrirDialogo = () => setDialogoVisible(!dialogoVisible);
  const abrirDetalle = () => setDetalleVisible(!detalleVisible);
  //funciones para manejar el cuadro de confirmacion
  const confirmarBorrado = (idImg, keyImg, titulo) => {
    setImagenTitulo(titulo);
    setKeyImagen(keyImg)
    setIdImagen(idImg);
    abrirDialogo();
  }
  //funcion para abrir el detalle de la imagen
  const verDetalle = async (idImg) => {
    await buscarImagen(idImg)
    abrirDetalle();
  }

  return (
    <div>
      <TableContainer style={{maxHeight: 500}}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell>Titulo</TableCell>
              <TableCell>Tipo</TableCell>
              <TableCell align="center">Novela</TableCell>
              <TableCell align="center">Subido Por</TableCell>
              <TableCell align="center">Actualizado</TableCell>
              <TableCell align="center">Acciones</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
          {listaImagenes.length > 0 ?
            listaImagenes.map((imagen, i) => (
              <TableRow key={i}>
                <TableCell>{imagen.titulo}</TableCell>
                <TableCell>{imagen.tipo}</TableCell>
                <TableCell>{imagen.novela?imagen.novela.titulo?imagen.novela.titulo:'-':'-'}</TableCell>
                <TableCell align="center">{imagen.subidoPor?imagen.subidoPor.username:'-'}</TableCell>
                <TableCell align="center">{moment(imagen.updatedAt).format('DD-MM-YYYY')}</TableCell>
                <TableCell>
                  <ButtonGroup>
                    <Button onClick={() => verDetalle(imagen._id)}>
                      <RemoveRedEye style={{ color: green[500] }}/>
                    </Button>
                    <Button onClick={() => confirmarBorrado(imagen._id, imagen.key, imagen.titulo)}>
                      <Delete style={{ color: red[500] }} />
                    </Button>
                  </ButtonGroup>
                </TableCell>
              </TableRow>
            ))
            :
            <TableRow style={{ height: 120 }}>
              <TableCell>Sin datos.</TableCell>
            </TableRow>
          }
          </TableBody>
        </Table>
      </TableContainer>
      <Table>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={[30, 50, 100]}
              colSpan={3}
              count={total}
              rowsPerPage={rowsPerPage}
              page={page}
              SelectProps={{
                inputProps: { 'aria-label': 'rows per page' },
                native: true,
              }}
              onChangePage={cambiarPagina}
              onChangeRowsPerPage={cambiarLimite}
            />
          </TableRow>
        </TableFooter>
      </Table>
      {/* DIALOGO PARA ELIMINAR CAPITULO */}
      <Dialogo
        titulo={`Eliminar '${imagenTitulo}'`}
        cuerpo="Esta accin no se puede deshacer."
        abrir={abrirDialogo}
        visible={dialogoVisible}
        accion={eliminar}
      />
      {/* DIALOGO PARA VER EL DETALLE DE LA IMAGEN */}
      <DialogoImagen
        visible={detalleVisible}
        abrir={abrirDetalle}
        imagen={imagenObj}
        loading={loadingImagen}
      />
    </div>
  )
}

TablaImagenes.propTypes = {
  listaImagenes: PropTypes.array,
  borrarImagen: PropTypes.func,
}

export default TablaImagenes

