import React from 'react'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Button, List, ListItem, ListItemText, MenuItem, Menu } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
  },
}));

function ListaCapitulos({novela={}, capitulos=[], guardar}) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [selectedIndex, setSelectedIndex] = React.useState(null);

  async function guardarCapitulo() {
    if (capitulos.length>0 && selectedIndex !== null) {
      await guardar(capitulos[selectedIndex]._id)
    }
  }

  const handleClickListItem = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuItemClick = (event, index) => {
    setSelectedIndex(index);
    setAnchorEl(null);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={classes.root}>
      <Typography color="secondary" style={{marginBottom: '1rem'}}>
        Editar el capitulo en emisin
      </Typography>
      <List component="nav" aria-label="Lista de Capitulos">
        <ListItem
          button
          aria-haspopup="true"
          aria-controls="lock-menu"
          aria-label="lista de capitulos"
          onClick={handleClickListItem}
        >
          <ListItemText primary="Lista de Capitulos" secondary={
            selectedIndex===null?
              novela.capitulo_emision?`${novela.capitulo_emision.numero} - ${novela.capitulo_emision.titulo}`:'-'
            :capitulos.length>0?`${capitulos[selectedIndex].numero} - ${capitulos[selectedIndex].titulo}`:'-'
          } 
          />
        </ListItem>
      </List>
      <Menu
        id="lock-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
      {capitulos.length > 0 ?
        capitulos.map((capitulo, index) => (
          <MenuItem
            key={capitulo._id}
            selected={index === selectedIndex}
            onClick={(event) => handleMenuItemClick(event, index)}
          >
            {capitulo.numero} - {capitulo.titulo}
          </MenuItem>
        ))
        :
        <MenuItem>Sin datos.</MenuItem>
      }
      </Menu>
      <Button variant="contained" color="primary" onClick={guardarCapitulo}>
        Guardar
      </Button>
    </div>
  )
}

ListaCapitulos.propTypes = {
  novela: PropTypes.object,
  capitulos: PropTypes.array,
  guardar: PropTypes.func
}

export default ListaCapitulos

