import React, { useState, useEffect } from 'react';
import { useHistory, withRouter } from 'react-router-dom';
import { Switch } from '@material-ui/core';

import Titulo from '../../components/common/Titulo';
import FormularioCapitulo from '../../components/capitulos/FormularioCapitulo';
import { getCapitulo, patchCapitulo } from '../../services/apiCapitulos';
import { patchNovelas } from '../../services/apiNovelas';

function EditarCapitulo({match}) {
  const history = useHistory();
  const [emision, setEmision] = useState(false);
  const [objCapitulo, setObjCapitulo] = useState({});
  const [novelaId, setNovelaId] = useState(null);
  const { id } = match.params;

  useEffect(() => {
    if (id) {
      cargarCapitulo(id);
    }
  },[id]);

  async function cargarCapitulo(cap) {
    await getCapitulo(cap)
      .then(({data}) => {
        if (Object.entries(data).length > 0) {
          setObjCapitulo(data);
          setNovelaId(data.novela?data.novela._id:null);
        }
      }).catch((error) => console.log(error));
  }

  async function actualizarCapitulo(objCapitulo) {
    if (id && objCapitulo) {
      return await patchCapitulo(id, objCapitulo).then(async({data}) => {
        if (emision && data) {
          return await patchNovelas(null, {id: novelaId, chapter: data._id}, 'actualizarCapituloEmision')
            .then(() => {
              setEmision(false);
              history.goBack();
            });
        } else {
          return true
        }
      });
    };
  }

  const cancelarForm = () => history.goBack();

  return (
    <div>
      <Titulo>Editar Capitulo</Titulo>
      <p>Capitulo en Emisin</p>
      <Switch
        checked={emision}
        onChange={e => setEmision(e.target.checked)}
        color="primary"
        name="emision"
        inputProps={{ 'aria-label': 'primary checkbox' }}
      />
      <FormularioCapitulo
        IdNovela={novelaId}
        capitulo={objCapitulo}
        guardar={actualizarCapitulo}
        cancelar={cancelarForm}
      />
    </div>
  )
};

export default withRouter(EditarCapitulo);
