import React, { useState, useEffect } from "react";
import { useHistory } from 'react-router-dom';
import Cookie from "js-cookie";
// import ServerCookie from "next-cookies";
import { 
  CircularProgress, Avatar, Button,
  TextField, Box, Typography,
  Container, Grid
} from "@material-ui/core";
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { makeStyles } from '@material-ui/core/styles';

import Copyright from "../components/common/Copyright";
import { login } from '../services/apiUsuario'
import constantes from "../config/constantes";
import { AuthToken } from "../services/authToken";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function Login() {
  const classes = useStyles();
  const history = useHistory();
  const [inputs, setInputs] = useState({ username: "", password: "" });
  const [error, setError] = useState({ username: false, password: false });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const token = Cookie.get(constantes.TOKEN_STORAGE_KEY);
    const auth = new AuthToken(token);
    if (token || auth.isValid) {
      history.push("/cms/dashboard");
    }
  //eslint-disable-next-line
  }, [])

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    await login(inputs).then(({data, errors}) => {
      setLoading(false);
      if (!data) {
        return setError(errors);
      } else {
        Cookie.set(constantes.TOKEN_STORAGE_KEY, data);
        history.push('/cms/dashboard');
      }
    }).catch(error => {
      console.log(error);
      return setLoading(false);
    })
  };

  const handleInputChange = (e) => {
    e.persist();
    setInputs({
      ...inputs,
      [e.target.name]: e.target.value,
    });
  };

  return (
    <Container component="main" maxWidth="xs">
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          TNO Cms
        </Typography>
        <form className={classes.form} noValidate={false} onSubmit={handleSubmit}>
          <TextField
            onChange={handleInputChange}
            value={inputs.username}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            label="Usuario"
            name="username"
            autoComplete="username"
            autoFocus
            //errores
            error={error.username?true:false}
            helperText={error.username}
          />
          <TextField
            onChange={handleInputChange}
            value={inputs.password}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            //errores
            error={error.password?true:false}
            helperText={error.password}
          />
          {
            loading ? 
            <Grid container direction="row" justify="center" alignItems="center">
              <CircularProgress  color="primary" className={classes.submit}/>
            </Grid>
            :
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              Acceder
            </Button>
          }
        </form>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
  );
};
