import React, { Component } from "react";
import Cookie from "js-cookie";
// import { AuthToken } from "../../services/authToken";
import constantes from "../../config/constantes";
import { session } from "../../services/apiUsuario";

export default function PrivateRoute(WrappedComponent) {
  
  return class extends Component {    
    async componentDidMount() {
      const token = Cookie.get(constantes.TOKEN_STORAGE_KEY);
      if (!token) {
        document.location.href = ('/cms/login');
      } else {
        await session().then((data) => {
          if(data.status === 'error') {
            Cookie.remove(constantes.TOKEN_STORAGE_KEY);
            document.location.href = ('/cms/login');
          }
        }).catch((error) => {
          console.log(error);
          console.log('error de sesion en (PrivateRoutes)');
        });
      }
    }

    render() {
      return <WrappedComponent {...this.props}/>;
    }
  };
};