import { restPrivate } from './requestHelper';

/**
 * @param {string} idCapitulo
 */
export const getCapitulo = async (idCapitulo) => restPrivate({
  url: `chapters/chapter/${idCapitulo}`, method: 'GET'
});
/**
 * @param {number} page 
 * @param {number} limit 
 */
export const getCapitulos = async(page, limit) => restPrivate({
  url: 'chapters', method: 'GET', params: {page, limit}
})
/**
 * @param {number} page
 * @param {number} limit
 * @param {string} novela
 */
export const getCapitulosNovela = async (page, limit, novela) => restPrivate({
  url: 'chapters/novel', method: 'GET', params: {page, limit, novela}
});
/**
 * @param {string} id
 */
export const getCapitulosLista = async (id) => restPrivate({
  url: 'chapters/list', method: 'GET', params:{id}
})
/**
 * @param {object} data - La data a enviar
 */
export const postCapitulo = async (data) => restPrivate({
  url: 'chapters', method: 'POST', data
})
/**
 * @param {id} id id de l capitulo
 * @param {data} data data del capitulo a actualizar
 */
export const patchCapitulo = async (id, data) => restPrivate({
  url: 'chapters', method: 'PATCH', data: {data, id}, headers:{ Metodo: 'actualizarCapitulo'}
})
/**
 * @param {string} idCapitulo
 */
export const deleteCapitulo = async (idCapitulo) => restPrivate({
  url: 'chapters', method: 'DELETE', params: { id: idCapitulo }, headers: { Metodo: 'borrarCapitulo' }
})