import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { TextField, MenuItem, Button, Dialog, DialogTitle, DialogContent } from '@material-ui/core';

function FormularioExtra({accion, objeto={}, guardar, abrir, visible}) {
  const [nombre, setNombre] = useState('');
  const [descripcion, setDescripcion] = useState('');
  const [tipo, setTipo] = useState(1);

  useEffect(() => {
    if(objeto.nombre !== undefined) {
      setNombre(objeto.nombre);
      setDescripcion(objeto.descripcion);
      setTipo(objeto.tipo);
    }
  },[objeto]);

  const submit = async (e) => {
    e.preventDefault();
    await guardar({nombre, descripcion, tipo});
    limpiar();
  };

  const limpiar = () => {
    setNombre('');
    setDescripcion('');
    setTipo(1);
  }

  return (
    <Dialog onClose={abrir} aria-labelledby="simple-dialog-title" open={visible}>
      <DialogTitle id="simple-dialog-title">{accion}</DialogTitle>
      <DialogContent>
        <form autoComplete="off" style={{ marginRight: '1rem' }} onSubmit={submit}>
          <div>
            <TextField 
              required 
              fullWidth
              style={{ margin: 8 }}
              id="nombre" 
              label="Nombre" 
              value={nombre}
              onChange={(e) => setNombre(e.target.value)}
            />
            <TextField 
              required 
              fullWidth
              style={{ margin: 8 }}
              id="descripcion" 
              label="Descripcin" 
              value={descripcion}
              onChange={(e) => setDescripcion(e.target.value)}
            />
            <TextField
              required
              fullWidth
              style={{ margin: 8 }}
              id="tipo"
              select
              label="Selecciona"
              value={tipo}
              onChange={(e) => setTipo(e.target.value)}
            >
              <MenuItem value={1}>Tipo</MenuItem>
              <MenuItem value={2}>Categoria</MenuItem>
              <MenuItem value={3}>Etiqueta</MenuItem>
            </TextField>
          </div>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            style={{ margin: '1rem' }}
          >
            Guardar
          </Button>
          <Button
            onClick={abrir}
            variant="contained"
            color="default"
          >
            Cancelar
          </Button>
        </form>
      </DialogContent>
    </Dialog>
  )
}

FormularioExtra.propTypes = {
  accion: PropTypes.string,
  objeto: PropTypes.object,
  guardar: PropTypes.func,
  abrir: PropTypes.func,
  visible: PropTypes.bool
}

export default FormularioExtra

