import { restPrivate } from './requestHelper';

/**
 * Metodo para obtener todas los extras 
 */
export const getExtras = async () =>  restPrivate({
  url: 'extras/all', method: 'GET'
});
/**
 * @param {number} tipo - tipo del extra
 */
export const getExtra = async (tipo) =>  restPrivate({
  url: 'extras', method: 'GET', params: { tipo }
});
/**
 * @param {object} data - nombre, descripcion y tipo
 */
export const postExtra = async (data) => restPrivate({
  url: 'extras', method: 'POST', data
});
/**
 * @param {object} data - nombre, descripcion y tipo
 * @param {string} id - id del extra
 */
export const patchExtra = async (data, id) => restPrivate({
  url: 'extras', method: 'PATCH', data: { data, id }, headers: { Metodo: 'actualizarExtra'}
});
/**
 * @param {string} id
 */
export const deleteExtra = async (id) => restPrivate({
  url: 'extras', method: 'DELETE', params: {id}, headers: {Metodo: 'borrarExtra'}
})
