import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';
import { 
  Table, TableContainer, TablePagination,
  TableHead, TableBody, TableFooter, 
  TableRow, TableCell, ButtonGroup, Button,  
} from '@material-ui/core';
import { green, cyan, red } from '@material-ui/core/colors';
import { AddCircle, FolderOpen, PermMedia, Edit, Delete } from '@material-ui/icons';

import { getNovelas, deleteNovela } from '../../services/apiNovelas';
import Titulo from '../../components/common/Titulo';
import Dialogo from '../../components/common/Dialogo';

export default function Novelas() {
  const [listaNovelas, setListaNovelas] = useState([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [total, setTotal] = useState(1);
  const [dialogoVisible, setDialogoVisible] = useState(false);
  const [idNovela, setIdNovela] = useState(null);
  const [novelaTitulo, setNovelaTitulo] = useState('')

  useEffect(() => {
    cargarNovelas(1,10);
  },[]);
  //cargar las novelas
  async function cargarNovelas(p,l) {
    await getNovelas({page: p, limit: l}).then(({data}) => {
      if (data.docs && (data.docs).length > 0) {
        setListaNovelas(data.docs);
        setTotal(data.totalDocs);
      }
    }).catch((error) => console.log(error));
  }
  //borrar novela
  async function borrarNovela() {
    if (idNovela) {
      await deleteNovela(idNovela).then(async() => {
        abrirDialogo();
        await cargarNovelas(Number(page)+1, rowsPerPage);
      });
    } else {
      abrirDialogo();
    }
  }

  const cambiarPagina = async (event, newPage) => {
    setPage(newPage);
    await cargarNovelas(Number(newPage)+1, rowsPerPage);
  };

  const cambiarLimite = async (event) => {
    let limite = parseInt(event.target.value, 10);
    setRowsPerPage(limite);
    setPage(0);
    await cargarNovelas(1, limite)
  };
  //funcion para manejar el estado del dialogo
  const abrirDialogo = () => setDialogoVisible(!dialogoVisible);
  //funcion para guardar la id de la novela y abrir el dialogo
  const confirmarBorrado = (id, titulo) => {
    setNovelaTitulo(titulo)
    setIdNovela (id);
    abrirDialogo();
  }

  return (
    <div>
      <Titulo>Lista de Novelas</Titulo>
      <Button 
        component={Link}
        to="/cms/novela/crear"
        variant="contained" 
        startIcon={<AddCircle/>} 
        style={{marginBottom: '.5rem', backgroundColor: green[500], color: 'white'}}
      >
        Nuevo
      </Button>
      <TableContainer style={{maxHeight: 500}}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell>#</TableCell>
              <TableCell>Titulo</TableCell>
              <TableCell>Tipo</TableCell>
              <TableCell>Estado</TableCell>
              <TableCell align="center">Activo</TableCell>
              <TableCell align="center">Capitulo Emisin</TableCell>
              <TableCell>Actualizado</TableCell>
              <TableCell align="center">Folder</TableCell>
              <TableCell align="center">Acciones</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
          {listaNovelas.length > 0 ?
            listaNovelas.map((novela, i) => (
              <TableRow key={i}>
                <TableCell>{i+1}</TableCell>
                <TableCell>{novela.titulo}</TableCell>
                <TableCell>{novela.tipo}</TableCell>
                <TableCell>{novela.estado}</TableCell>
                <TableCell align="center">{novela.activo? 'Si':'No'}</TableCell>
                <TableCell align="center">{novela.capitulo_emision?novela.capitulo_emision.numero:'-'}</TableCell>
                <TableCell>{moment(novela.updatedAt).format('DD-MM-YYYY')}</TableCell>
                <TableCell>
                  <ButtonGroup size="small">
                    <Button component={Link} to={`/cms/novela/capitulos/${novela._id}`} params={{ id: novela._id }}>
                      <FolderOpen color="secondary"/>
                    </Button>
                    <Button component={Link} to={`/cms/novela/imagenes/${novela._id}`} params={{ id: novela._id }}>
                      <PermMedia color="primary" />
                    </Button>
                  </ButtonGroup>
                </TableCell>
                <TableCell>
                  <ButtonGroup size="small">
                    <Button component={Link} to={`/cms/novela/editar/${novela._id}`} params={{ id: novela._id }}>
                      <Edit style={{ color: cyan[500] }}/>
                    </Button>
                    <Button onClick={() => confirmarBorrado(novela._id, novela.titulo)}>
                      <Delete style={{ color: red[500] }} />
                    </Button>
                  </ButtonGroup>
                </TableCell>
              </TableRow>
            ))
            :
            <TableRow style={{ height: 120 }}>
              <TableCell>Sin datos.</TableCell>
            </TableRow>
          }
          </TableBody>
        </Table>
      </TableContainer>
      <Table>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={[10, 25, 50, 100]}
              colSpan={3}
              count={total}
              rowsPerPage={rowsPerPage}
              page={page}
              SelectProps={{
                inputProps: { 'aria-label': 'rows per page' },
                native: true,
              }}
              onChangePage={cambiarPagina}
              onChangeRowsPerPage={cambiarLimite}
            />
          </TableRow>
        </TableFooter>
      </Table>
      {/* DIALOGO PARA ELIMINAR NOVELA */}
      <Dialogo
        titulo={`Eliminar '${novelaTitulo}'`}
        cuerpo="Esta accin eliminar la novela de la base de datos, si la novela tiene capitulos o imagenes vinculadas se pondra en estado 'inactivo'."
        abrir={abrirDialogo}
        visible={dialogoVisible}
        accion={borrarNovela}
      />
    </div>
  );
}
