import React from 'react';
import PropTypes from 'prop-types';
import { WithContext as ReactTags } from 'react-tag-input';

const delimiters = [188, 13];

function Etiquetas({sugerencias=[], etiquetas=[], guardar}) {

  const handleDelete = (i) => {
    const tags = etiquetas.filter((tag, index) => index !== i);
    guardar(tags);
  }

  const handleAddition = (tag) => {
    const tags = [...etiquetas, tag]
    guardar(tags);
  }

  const handleDrag = (tag, currPos, newPos) => {
    const tags = etiquetas;
    const newTags = tags.slice();

    newTags.splice(currPos, 1);
    newTags.splice(newPos, 0, tag);

    // re-render
    guardar(newTags);
  }

  return (
    <div style={{ margin: 8 }} >
      <ReactTags
        tags={etiquetas}
        suggestions={sugerencias}
        delimiters={delimiters}
        handleDelete={handleDelete}
        handleAddition={handleAddition}
        handleDrag={handleDrag}
        autofocus={false}
        inputFieldPosition="bottom"
      />
    </div>
  )
}

Etiquetas.propTypes = {
  sugerencias: PropTypes.array,
  etiquetas: PropTypes.array,
  guardar: PropTypes.func
}

export default Etiquetas

