import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { Accordion, AccordionDetails, AccordionSummary, Typography } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}));

function AcordionTemplate({abierto, abrir, children, titulo, subtitulo, panel}) {
  const classes = useStyles();

  return (
    <Accordion expanded={abierto === panel} onChange={abrir(panel)}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1bh-content"
        id="panel1bh-header"
      >
        <Typography className={classes.heading}>{titulo}</Typography>
        <Typography className={classes.secondaryHeading}>{subtitulo}</Typography>
      </AccordionSummary>
      <AccordionDetails>
      {children}
      </AccordionDetails>
    </Accordion>
  )
}

AcordionTemplate.propTypes = {
  abierto: PropTypes.any,
  abrir: PropTypes.func,
  titulo: PropTypes.string,
  subtitulo: PropTypes.string,
  panel: PropTypes.string
}

export default AcordionTemplate

