import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { 
  Button, Dialog, DialogActions,
  DialogContent, DialogTitle,
  CircularProgress, Card, CardMedia,
  CardContent, Typography
} from '@material-ui/core';

function DialogoImagen({visible, abrir, imagen={}, loading}) {
  const [alto, setAlto] = useState(100);
  const [ancho, setAncho] = useState(100);

  useEffect(() => {
    if(imagen.url !== undefined && imagen.url !== null) {
      let img = new Image();
      img.src = imagen.url;
      img.onload = function() {
        setAlto(this.height);
        setAncho(this.width);
      }
    }
  },[imagen]);

  return (
    <Dialog
      open={visible}
      onClose={abrir}
      aria-labelledby="detalle-imagen"
      aria-describedby="detalle-imagen-seleccionada"
    >
      <DialogTitle id="detalle-imagen">{"Detalle de la imagen"}</DialogTitle>
      <DialogContent>
      {loading?
        <CircularProgress />
        :
        <Card>
          <CardMedia
            style={{height: alto, width: ancho}}
            image={imagen.url}
            title={imagen.titulo}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
            {imagen.titulo}
            </Typography>
            <Typography variant="h6" color="textSecondary" component="h6">Tipo:</Typography>
            <Typography variant="body2" color="textSecondary" component="p">{imagen.tipo}</Typography>
            <Typography variant="h6" color="textSecondary" component="h6">Extencin:</Typography>
            <Typography variant="body2" color="textSecondary" component="p">{imagen.contentType}</Typography>
            <Typography variant="h6" color="textSecondary" component="h6">Key:</Typography>
            <Typography variant="body2" color="textSecondary" component="p">{imagen.key}</Typography>
          </CardContent>
        </Card>
      }
      </DialogContent>
      <DialogActions>
        <Button onClick={abrir} color="primary" autoFocus>
          Aceptar
        </Button>
      </DialogActions>
    </Dialog>
  )
}

DialogoImagen.propTypes = {
  visible: PropTypes.bool,
  abrir: PropTypes.func,
  imagen: PropTypes.object,
  loading: PropTypes.bool
}

export default DialogoImagen

