import React, { useState } from 'react';
import { useHistory, withRouter } from 'react-router-dom';
import { Switch } from '@material-ui/core';

import Titulo from '../../components/common/Titulo';
import FormularioCapitulo from '../../components/capitulos/FormularioCapitulo';
import { postCapitulo } from '../../services/apiCapitulos';
import { patchNovelas } from '../../services/apiNovelas';

function CrearCapitulo({match}) {
  const history = useHistory();
  const [emision, setEmision] = useState(false);
  const { id } = match.params;

  async function guardarCapitulo(data) {
    return await postCapitulo(data).then(async({data}) => {
      if (emision && data) {
        return await patchNovelas(null, {id: id, chapter: data._id}, 'actualizarCapituloEmision')
          .then(() => setEmision(false));
      } else {
        return true
      }
    })
  }

  const cancelarForm = () => history.goBack();

  return (
    <div>
      <Titulo>Crear Capitulo</Titulo>
      <p>Capitulo en Emisin</p>
      <Switch
        checked={emision}
        onChange={e => setEmision(e.target.checked)}
        color="primary"
        name="emision"
        inputProps={{ 'aria-label': 'primary checkbox' }}
      />
      <FormularioCapitulo
        IdNovela={id}
        guardar={guardarCapitulo}
        cancelar={cancelarForm}
      />
    </div>
  )
};

export default withRouter(CrearCapitulo);
