import { restPrivate } from './requestHelper';

/**
 * @param {number} page
 * @param {number} limit
 */
export const getNovelas = async (params) => restPrivate({
  url: 'novels', method: 'GET', params
});
/**
 * @param {string} id - id de la novela
 */
export const getNovela = async (id) => restPrivate({
  url: `novels/${id}`, method: 'GET'
});
/**
 * @param {data} data - la data a enviar
 */
export const postNovelas = async (data) => restPrivate({
  url: 'novels', method: 'POST', data
})
/**
 * @param {object} data - la data a enviar
 * @param {object} params - id, chapter, image, extencion, tipo (de imagen)
 * @param {string} metodo - actualizarNovela, actualizarCapituloEmision, actualizarImagenes, activarNovela
 */
export const patchNovelas = async (data, params,  metodo) => restPrivate({
  url: 'novels', method: 'PATCH', data, params: { ...params}, headers: { Metodo: metodo}
})
/**
 * @param {string} idNovela
 */
export const deleteNovela = async (idNovela) => restPrivate({
  url: 'novels', method: 'DELETE', params: {id: idNovela}, headers: {Metodo: 'borrarNovela'}
})
