import React from 'react';
import PropTypes from 'prop-types';
import { 
  Button, Dialog, DialogActions,
  DialogContent, DialogContentText, 
  DialogTitle, Slide
} from '@material-ui/core';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function Dialogo({visible, abrir, titulo, cuerpo, accion}) {

  return (
    <Dialog
      open={visible}
      TransitionComponent={Transition}
      keepMounted
      onClose={abrir}
      aria-labelledby="alert-dialog-slide-title"
      aria-describedby="alert-dialog-slide-description"
    >
      <DialogTitle id="alert-dialog-slide-title">{titulo}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-slide-description">
          {cuerpo}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={abrir} color="primary">
          Cancelar
        </Button>
        <Button onClick={accion} color="primary">
          Aceptar
        </Button>
      </DialogActions>
    </Dialog>
  );
};

Dialogo.propTypes = {
  visible: PropTypes.bool,
  abrir: PropTypes.func,
  titulo: PropTypes.string,
  cuerpo: PropTypes.string,
  accion: PropTypes.func
}