import { restAuth, restPrivate } from './requestHelper'

export const login = async (inputs) =>  restAuth({
  url: 'auth/login', method: 'POST', data: new URLSearchParams(inputs)
});

export const session = async () => restPrivate({
  url: 'auth/session', method: 'GET'
});

export const logout = async () => restPrivate({
  url: 'auth/logout', method: 'GET'
});