import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Typography, FormControl, InputLabel, Select, MenuItem, FormHelperText, Button } from '@material-ui/core';

function ListaImagenes({imagenes=[], actualizarImagen}) {
  const [imagenSeleccionada, setImagenSeleccionada] = useState('');
  const [tipo, setTipo] = useState('');

  const guardarImagen = async () => {
    if (imagenSeleccionada !== '') {
      let img = imagenes[imagenSeleccionada];
      await actualizarImagen(img.url, img.contentType, tipo);
    }
  }

  return (
    <div>
      <Typography color="secondary" style={{marginBottom: '1rem'}}>
        Editar imagenes
      </Typography>
      <FormControl style={{minWidth: 120, margin: '1rem'}}>
        <InputLabel id="seleccionar-imagen">Imagen</InputLabel>
        <Select
          labelId="seleccionar-imagen"
          id="seleccion-imagen"
          value={imagenSeleccionada}
          onChange={(e) => setImagenSeleccionada(e.target.value)}
        >
        {
          imagenes.length > 0 ?
          imagenes.map((imagen, i) => (
            <MenuItem key={i} value={i}>{`${imagen.titulo} - ${imagen.tipo}`}</MenuItem>
          ))
          :
          (<MenuItem value="nada"><em>Sin datos</em></MenuItem>)
        }
        </Select>
        <FormHelperText>Selecciona la imagen</FormHelperText>
      </FormControl>
      <FormControl style={{minWidth: 120, margin: '1rem'}}>
        <InputLabel id="seleccionar-tipo">Tipo</InputLabel>
        <Select
          labelId="seleccionar-tipo"
          id="seleccion-de-tipo"
          value={tipo}
          onChange={(e) => setTipo(e.target.value)}
        >
          <MenuItem value="miniatura">Miniatura</MenuItem>
          <MenuItem value="portada">Portada</MenuItem>
        </Select>
        <FormHelperText>Selecciona el tipo de imagen</FormHelperText>
      </FormControl><br/>
      <Button variant="contained" color="primary" onClick={guardarImagen}>
        Guardar
      </Button>
    </div>
  )
}

ListaImagenes.propTypes = {
  novela: PropTypes.object,
  imagenes: PropTypes.array,
  actualizarImagen: PropTypes.func
}

export default ListaImagenes

