import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { TextField, Button } from '@material-ui/core';
import { red } from '@material-ui/core/colors';
import JoditEditor from 'jodit-react';

function FormularioCapitulo({IdNovela, capitulo={}, guardar, cancelar}) {
  const editor = useRef(null)
  const [titulo, setTitulo] = useState('');
  const [numero, setNumero] = useState(1);
  const [cuerpo, setCuerpo] = useState('');

  useEffect(() => {
    if (Object.entries(capitulo).length > 0) {
      setTitulo(capitulo.titulo?capitulo.titulo:'');
      setNumero(capitulo.numero?capitulo.numero:1);
      setCuerpo(capitulo.cuerpo?capitulo.cuerpo:'');
    }
  },[capitulo])

  const guardarCapitulo = async (e) => {
    e.preventDefault();
    if (IdNovela) {
      await guardar({titulo, numero, cuerpo, novela: IdNovela})
      limpiar();
    } else {
      console.log('no se reconoce el id de la novela')
    }
    
  }

  const guardarContenido = (e) => {
    setCuerpo(e.target.innerHTML)
  }

  const limpiar = () => {
    setTitulo('');
    setNumero(Number(numero)+1);
    setCuerpo('');
  }

  return (
    <form autoComplete="off" style={{ marginRight: '1rem' }} onSubmit={guardarCapitulo}>
      <div>
        <TextField 
          required 
          fullWidth
          autoFocus
          style={{ margin: 8 }}
          margin="dense"
          id="titulo_capitulo" 
          label="Titulo"
          value={titulo}
          onChange={(e) => setTitulo(e.target.value)}
        />
        <TextField 
          required 
          fullWidth
          style={{ margin: 8 }}
          type="number"
          margin="dense"
          id="numero" 
          label="Nmero del Capitulo" 
          value={numero}
          onChange={(e) => setNumero(e.target.value)}
        />
        <JoditEditor
          ref={editor}
          value={cuerpo}
          config={{ readonly: false}}
          onBlur={guardarContenido}
        />
      </div>
      <Button
        type="submit"
        variant="contained"
        color="primary"
        style={{ margin: '1rem' }}
      >
        Guardar
      </Button>
      <Button
        variant="contained"
        style={{ margin: '1rem 0rem', backgroundColor: red[500], color: 'white' }}
        onClick={cancelar}
      >
        Cancelar
      </Button>
    </form>
  )
}

FormularioCapitulo.propTypes = {
  IdNovela: PropTypes.string,
  capitulo: PropTypes.object
}

export default FormularioCapitulo

