import React from 'react';
import { useHistory } from 'react-router-dom'

import Titulo from '../../components/common/Titulo'
import FormularioNovela from '../../components/novelas/FormularioNovela'

import { postNovelas } from '../../services/apiNovelas';

export default function Crear() {
  const history = useHistory();

  async function crearNovela(data) {
    if (Object.entries(data).length > 0) {
      await postNovelas(data)
        .then(() => history.push('/cms/novela'))
        .catch(error => console.log(error));
    }
  }

  return (
    <div>
      <Titulo>Crear Novela</Titulo>
      <FormularioNovela
        guardar={crearNovela}
      />
    </div>
  )
}
