import { restPrivate } from "./requestHelper"

/**
 * @param {number} page 
 * @param {number} limit 
 */
export const getImages = async (page, limit) => restPrivate({
  url: 'images', method: 'GET', params: {page, limit}
});
//----------------------------------------------------------------------------------------//
/**
 * @param {number} page 
 * @param {number} limit 
 * @param {string} novela 
 */
export const getImagesNovel = async (page, limit, novela) => restPrivate({
  url: 'images/novel', method: 'GET', params: {page, limit, novela}
});
//----------------------------------------------------------------------------------------//
/**
 * @param {string} novela 
 */
export const getImagesLista = async (novela) => restPrivate({
  url: 'images/list', method: 'GET', params: { novela }
})
//----------------------------------------------------------------------------------------//
/**
 * @param {string} id 
 */
export const getImage = async (id) => restPrivate({
  url: `images/image/${id}`, method: 'GET'
})
//----------------------------------------------------------------------------------------//
/**
 * @param {object} data 
 */
export const postImagen = async(data) => restPrivate({
  url: 'images', method: 'POST', data, headers: { 'Content-Type': 'multipart/form-data' }
})
//----------------------------------------------------------------------------------------//
/**
 * @param {string} idImagen
 * @param {string} keyImg
 */
export const deleteImagen = async (idImagen, keyImg) => restPrivate({
  url: 'images', method: 'DELETE', params: { id: idImagen, key: keyImg }, headers: { Metodo: 'borrarImagen' }
})