// Route Views
import Dashboard from './views/dashboard';
//---------------------NOVELAS----------------------------------------------//
import Novelas from './views/novelas';
import CrearNovela from './views/novelas/crear';
import EditarNovela from './views/novelas/editar';
import CapitulosNovela from './views/novelas/capitulos';
import ImagenesNovela from './views/novelas/imagenes';
//---------------------CAPITULOS----------------------------------------------//
import Capitulos from './views/capitulos';
import CrearCapitulo from './views/capitulos/crear';
import EditarCapitulo from './views/capitulos/editar';
//---------------------IMAGENES----------------------------------------------//
import Imagenes from './views/imagenes';
import CrearImagen from './views/imagenes/crear';
//---------------------EXTRAS----------------------------------------------//
import Extras from './views/extras';

export default [
  {
    path: "/cms/dashboard",
    exact: true,
    component: Dashboard,
  },
  {
    path: '/cms/novela',
    exact: true,
    component: Novelas
  },
  {
    path: '/cms/novela/crear',
    exact: true,
    component: CrearNovela
  },
  {
    path: '/cms/novela/editar/:id',
    exact: true,
    component: EditarNovela
  },
  {
    path: '/cms/novela/capitulos/:id',
    exact: true,
    component: CapitulosNovela
  },
  {
    path: '/cms/novela/imagenes/:id',
    exact: true,
    component: ImagenesNovela
  },
  //capitulos
  {
    path: '/cms/capitulo',
    exact: true,
    component: Capitulos
  },
  {
    path: '/cms/capitulo/crear/:id',
    exact: true,
    component: CrearCapitulo
  },
  {
    path: '/cms/capitulo/editar/:id',
    exact: true,
    component: EditarCapitulo
  },
  //imagenes
  {
    path: '/cms/imagen',
    exact: true,
    component: Imagenes
  },
  {
    path: '/cms/imagen/crear/:id',
    exact: true,
    component: CrearImagen
  },
  //extras 
  {
    path: '/cms/extra',
    exact: true,
    component: Extras
  },
];
