import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { makeStyles } from '@material-ui/core/styles';
import { Card, CardContent, Typography  } from '@material-ui/core';

const useStyles = makeStyles({
  root: {
    minWidth: 275,
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
});

function AcordionDetalles({novela}) {
  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography 
          className={classes.title} 
          color="textSecondary" 
          gutterBottom
        >
          Subido Por:
        </Typography>
        <Typography 
          variant="h6" 
          component="h2"
          className={classes.pos} 
        >
          {novela.subidoPor?novela.subidoPor.username:'-'}
        </Typography>
        <Typography 
          className={classes.title} 
          color="textSecondary" 
          gutterBottom
        >
          Actualizado Por:
        </Typography>
        <Typography 
          variant="h6" 
          component="h2"
          className={classes.pos} 
        >
          {novela.actualizadoPor?novela.actualizadoPor.username:'-'}
        </Typography>
        <Typography 
          className={classes.title} 
          color="textSecondary" 
          gutterBottom
        >
          Fecha Creacin:
        </Typography>
        <Typography 
          variant="h6" 
          component="h2"
          className={classes.pos} 
        >
          {novela.createdAt?moment(novela.createdAt).format('DD-MM-YYYY'):'-'}
        </Typography>
        <Typography 
          className={classes.title} 
          color="textSecondary" 
          gutterBottom
        >
          Fecha Actualizado:
        </Typography>
        <Typography 
          variant="h6" 
          component="h2"
          className={classes.pos} 
        >
          {novela.updatedAt?moment(novela.updatedAt).format('DD-MM-YYYY'):'-'}
        </Typography>
        <Typography 
          className={classes.title} 
          color="textSecondary" 
          gutterBottom
        >
          Rating:
        </Typography>
        <Typography 
          variant="h6" 
          component="h2"
          className={classes.pos} 
        >
          Contador: {novela.rating?novela.rating.contador:'-'}{` `}
          Valor: {novela.rating?novela.rating.valor:'-'}
        </Typography>
      </CardContent>
    </Card>
  )
}

AcordionDetalles.propTypes = {
  novela: PropTypes.object
}

export default AcordionDetalles

