import React from 'react';
import { Link } from 'react-router-dom';
import { ListItem, ListItemIcon, ListItemText, Divider } from '@material-ui/core';
import { Dashboard, LibraryBooksOutlined, MenuBookOutlined, PhotoLibrary, Category } from '@material-ui/icons';

export const mainListItems = (
  <div>
    <ListItem button component={Link} to="/cms/dashboard">
      <ListItemIcon>
        <Dashboard />
      </ListItemIcon>
      <ListItemText primary="Dashboard" />
    </ListItem>
    <ListItem button component={Link} to="/cms/novela">
      <ListItemIcon>
        <LibraryBooksOutlined />
      </ListItemIcon>
      <ListItemText primary="Novelas" />
    </ListItem>
    <ListItem button component={Link} to="/cms/capitulo">
      <ListItemIcon>
        <MenuBookOutlined />
      </ListItemIcon>
      <ListItemText primary="Capitulos" />
    </ListItem>
    <ListItem button component={Link} to="/cms/imagen">
      <ListItemIcon>
        <PhotoLibrary />
      </ListItemIcon>
      <ListItemText primary="Imagenes" />
    </ListItem>
    <Divider/>
    <ListItem button component={Link} to="/cms/extra">
      <ListItemIcon>
        <Category />
      </ListItemIcon>
      <ListItemText primary="Extras" />
    </ListItem>
  </div>
);