import React from 'react';
import { useHistory } from "react-router-dom";
import clsx from 'clsx';
import Cookie from "js-cookie";
import { AppBar, Toolbar, Typography, IconButton } from '@material-ui/core';
import { Menu, ExitToApp } from '@material-ui/icons';

import useStyles from './stylesLayout';
import { logout } from '../../services/apiUsuario';
import constantes from '../../config/constantes';

export default function ToolbarMenu({abrirMenu, open}) {
  const history = useHistory();
  const classes = useStyles();

  const cerrarSesion = async () => {
    await logout().then(async() => {//cerr sesion correctamente
      Cookie.remove(constantes.TOKEN_STORAGE_KEY);
      history.push('/cms/login');
    }).catch(error => {
      console.log(error);
      console.log('Error cerrando sesin.')
    })
  }

  return (
    <AppBar position="absolute" className={clsx(classes.appBar, open && classes.appBarShift)}>
      <Toolbar className={classes.toolbar}>
        <IconButton
          edge="start"
          color="inherit"
          aria-label="open drawer"
          onClick={abrirMenu}
          className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
        >
          <Menu />
        </IconButton>
        {/* ELEMENTO VACIO PARA ACOMODAR EL ICONO LOGOUT EN LA ESQUINA DERECHA */}
        <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}/>
        <IconButton color="inherit" onClick={cerrarSesion}>
          <ExitToApp />
        </IconButton>
      </Toolbar>
    </AppBar>
  )
}
