import React, { useState, useEffect } from 'react';
import { useHistory, withRouter } from 'react-router-dom';

import FormularioNovela from '../../components/novelas/FormularioNovela';
import Titulo from '../../components/common/Titulo';
import AcordionTemplate from '../../components/novelas/AcordionTemplate';
import AcordionDetalles from '../../components/novelas/AcordionDetalles';
import ActivarNovela from '../../components/novelas/ActivarNovela';
import ListaImagenes from '../../components/novelas/ListaImagenes';
import ListaCapitulos from '../../components/novelas/ListaCapitulos';

import { getNovela, patchNovelas } from '../../services/apiNovelas';
import { getCapitulosLista } from '../../services/apiCapitulos';
import { getImagesLista } from '../../services/apiImagenes';


function Editar({match}) {
  const history = useHistory();
  const [novela, setNovela] = useState({});
  const [capitulos, setCapitulos] = useState([]);
  const [imagenes, setImagenes] = useState([]);
  const [abierto, setAbierto] = useState(false);
  const { id } = match.params;

  useEffect(() => {
    console.log(id);
    if(id) {
      cargarNovela(id);
      cargarListaCapitulos(id);
      cargarListaImagenes(id);
    }
  },[id]);

  async function cargarNovela(idNovela) {
    await getNovela(idNovela)
      .then(({data}) => {
        if (Object.entries(data).length>0) {
          setNovela(data)
        }
      })
  }

  async function cargarListaCapitulos(idNovela) {
    await getCapitulosLista(idNovela)
      .then(({data}) => {
        if (data.length>0) {
          setCapitulos(data)
        }
      })
  }

  async function cargarListaImagenes(idNovela) {
    await getImagesLista(idNovela).then(({data}) => {
      if(data.length>0) {
        setImagenes(data);
      }
    })
  }

  async function actualizarNovela(data) {
    await patchNovelas(data, {id}, 'actualizarNovela')
      .then(() => history.push('/cms/novela'))
  }

  async function actualizarCapituloEmision(idCapitulo) {
    await patchNovelas(null, { id, chapter: idCapitulo}, 'actualizarCapituloEmision')
  }
  /**
   * @param {string} image url de la imagen
   * @param {string} extencion ext de la imagen
   * @param {string} tipo miniatura o portada
   */
  async function actualizarImagen(image,extencion,tipo) {
    await patchNovelas(null, { id, image, extencion, tipo }, 'actualizarImagenes')
  }

  async function activarNovela() {
    await patchNovelas(null, {id}, 'activarNovela');
  }

  const abrir = (panel) => (event, isExpanded) => {
    setAbierto(isExpanded ? panel : false);
  };

  return (
    <div>
      <Titulo>Editar Novela</Titulo>
      <FormularioNovela
        guardar={actualizarNovela}
        novela={novela}
      />
      <AcordionTemplate
        abierto={abierto}
        abrir={abrir}
        titulo="Detalles"
        subtitulo="Datos extras de la novela"
        panel="panel1"
      >
        <AcordionDetalles
          novela={novela}
        />
      </AcordionTemplate>
      <AcordionTemplate
        abierto={abierto}
        abrir={abrir}
        titulo="Emisin"
        subtitulo="Editar Capitulo en emisin"
        panel="panel2"
      >
        <ListaCapitulos
          novela={novela}
          capitulos={capitulos}
          guardar={actualizarCapituloEmision}
        />
      </AcordionTemplate>
      <AcordionTemplate
        abierto={abierto}
        abrir={abrir}
        titulo="Imagenes"
        subtitulo="Editar imagenes de portada y miniatura"
        panel="panel3"
      >
        <ListaImagenes
          imagenes={imagenes}
          actualizarImagen={actualizarImagen}
        />
      </AcordionTemplate>
      <AcordionTemplate
        abierto={abierto}
        abrir={abrir}
        titulo="Configuracion"
        subtitulo="Activar novela"
        panel="panel4"
      >
        <ActivarNovela
          novela={novela}
          activar={activarNovela}
        />
      </AcordionTemplate>
    </div>
  )
};

export default withRouter(Editar)
