import React, { useState, useEffect } from 'react';
import moment from 'moment';
import { 
  Table, TableContainer, TableHead, 
  TableBody, TableRow, TableCell, 
  ButtonGroup, Button, Toolbar,
  List, ListItem, ListItemText,
  Menu, MenuItem
} from '@material-ui/core';
import { green, cyan, red } from '@material-ui/core/colors';
import { AddCircle, Edit, Delete } from '@material-ui/icons';

import Titulo from '../../components/common/Titulo';
import Dialogo from '../../components/common/Dialogo';
import FormularioExtra from '../../components/extras/FormularioExtra';
import { getExtra, postExtra, deleteExtra, patchExtra } from '../../services/apiExtras';

const options = [
  'Tipos',
  'Categorias',
  'Etiquetas',
];

export default function Index() {
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [listaExtras, setListaExtras] = useState([]);
  const [objetoExtra, setObjetoExtra] = useState({});
  const [idExtra, setIdExtra] = useState(null);
  const [nombre, setNombre] = useState(null);
  const [dialogoCrear, setDialogoCrear] = useState(false);
  const [dialogoActualizar, setDialogoActualizar] = useState(false);
  const [dialogoBorrar, setDialogoBorrar] = useState(false);

  useEffect(() => {
    listarExtras(0);
  },[]);
  //funcion para cargar los extras
  async function listarExtras(tipo) {
    await getExtra(Number(tipo)+1).then((data) => {
      setListaExtras(data.data)
    }).catch((error) => console.log(error));
  }
  //funcion para crear nuevo extra
  async function crearExtra(data) {
    await postExtra(data).then(async() => {
      abrirDialogoCrear()
      await listarExtras(selectedIndex);
    }).catch((error) => {
      console.log(error);
      abrirDialogoCrear()
    })
  }
  //funcion para actualizar el objeto
  async function actualizarExtra(data) {
    await patchExtra(data, idExtra).then(async() => {
      abrirDialogoActualizar()
      await listarExtras(selectedIndex);
    }).catch((error) => {
      console.log(error);
      abrirDialogoActualizar()
    })
  }
  //funcion para borrar el objeto
  async function borrarExtra() {
    await deleteExtra(idExtra).then(async() => {
      setDialogoBorrar(false);
      await listarExtras(selectedIndex);
    }).catch((error) => {
      setDialogoBorrar(false);
      console.log(error);
    });
  }

  //funciones para manejar el toolbar
  const handleClickListItem = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);
  const handleMenuItemClick = async (event, index) => {
    setSelectedIndex(index);
    await listarExtras(index);
    setAnchorEl(null);
  };
  //funciones para abrir los dialogos
  const abrirDialogoCrear = () => setDialogoCrear(!dialogoCrear);
  const abrirDialogoActualizar = () => setDialogoActualizar(!dialogoActualizar);
  const abrirDialogoBorrar = () => setDialogoBorrar(!dialogoBorrar);
  //manejar el dialogo para actualizar
  const dialogoActualizarExtra = (obj) => {
    setObjetoExtra(obj);
    setIdExtra(obj._id);
    abrirDialogoActualizar();
  }
  //manejar el dialogo para eliminar
  const dialogoBorrarExtra = (id, nombre) => {
    setIdExtra(id);
    setNombre(nombre);
    abrirDialogoBorrar();
  }

  return (
    <div>
      <Toolbar>
        <Titulo>Lista de Extras</Titulo>
        <List component="nav" aria-label="Tipos de extras">
          <ListItem
            button
            aria-haspopup="true"
            aria-controls="lock-menu"
            aria-label="seleccionar extra"
            onClick={handleClickListItem}
          >
            <ListItemText primary="Seleccionar extra" secondary={options[selectedIndex]} />
          </ListItem>
        </List>
        <Menu
          id="lock-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          {options.map((option, index) => (
            <MenuItem
              key={option}
              selected={index === selectedIndex}
              onClick={(event) => handleMenuItemClick(event, index)}
            >
              {option}
            </MenuItem>
          ))}
        </Menu>
      </Toolbar>
      <Button 
        variant="contained" 
        startIcon={<AddCircle/>} 
        style={{marginBottom: '.5rem', backgroundColor: green[500], color: 'white'}}
        onClick={abrirDialogoCrear}
      >
        Nuevo
      </Button>
      <TableContainer style={{maxHeight: 500}}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell>#</TableCell>
              <TableCell>Nombre</TableCell>
              <TableCell>Descripcion</TableCell>
              <TableCell>Acrualizado</TableCell>
              <TableCell align="center">Acciones</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
          {listaExtras.length > 0 ?
            listaExtras.map((extra, i) => (
              <TableRow key={i}>
                <TableCell>{i+1}</TableCell>
                <TableCell>{extra.nombre}</TableCell>
                <TableCell>{extra.descripcion}</TableCell>
                <TableCell>{moment(extra.updatedAt).format('DD-MM-YYYY')}</TableCell>
                <TableCell align="center">
                  <ButtonGroup size="small">
                    <Button onClick={() => dialogoActualizarExtra(extra)}>
                      <Edit style={{ color: cyan[500] }}/>
                    </Button>
                    <Button onClick={() => dialogoBorrarExtra(extra._id, extra.nombre)}>
                      <Delete style={{ color: red[500] }} />
                    </Button>
                  </ButtonGroup>
                </TableCell>
              </TableRow>
            ))
            :
            <TableRow style={{ height: 120 }}>
              <TableCell>Sin datos.</TableCell>
            </TableRow>
          }
          </TableBody>
        </Table>
      </TableContainer>
      {/* DIALOGO PARA CREAR NUEVO EXTRA */}
      <FormularioExtra
        key="Crear"
        accion="Crear"
        abrir={abrirDialogoCrear}
        visible={dialogoCrear}
        guardar={crearExtra}
      />
      {/* DIALOGO PARA ACTUALIZAR EL OBJETO */}
      <FormularioExtra
        key="Actualizar"
        accion="Actualizar"
        objeto={objetoExtra}
        abrir={abrirDialogoActualizar}
        visible={dialogoActualizar}
        guardar={actualizarExtra}
      />
      {/* DIALOGO PARA ACTUALIZAR NUEVO EXTRA */}
      <Dialogo
        titulo={`Eliminar '${nombre}'`}
        cuerpo="Esta accin eliminar el objeto seleccionado."
        abrir={abrirDialogoBorrar}
        visible={dialogoBorrar}
        accion={borrarExtra}
      />
      
      
    </div>
  )
}
