import React, { useState, useEffect } from 'react';
import { Link, withRouter } from 'react-router-dom'
import { Button } from '@material-ui/core';
import { AddCircle } from '@material-ui/icons';
import { green } from '@material-ui/core/colors';

import Titulo from '../../components/common/Titulo';
import TablaImagenes from '../../components/imagenes/TablaImagenes';
import { getImagesNovel, deleteImagen } from '../../services/apiImagenes';

function ListaImagenes({match}) {
  const [listaImagenes, setListaImagenes] = useState([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(30);
  const [total, setTotal] = useState(1);
  const { id } = match.params;

  useEffect(() => {
    if (id) {
      cargarImagenes(1, 30, id);
    }
  },[id]);

  async function cargarImagenes(p, l, n) {
    await getImagesNovel(p, l, n).then(({data}) => {
      if (data.docs && (data.docs).length > 0) {
        setListaImagenes(data.docs);
        setTotal(data.totalDocs);
      }
    })
  }
  //funcion para eliminar la imagen
  async function borrarImagen(idImg, keyImg) {
    if (idImg && keyImg) {
      return await deleteImagen(idImg, keyImg)
      .then(async () => {
        await cargarImagenes(Number(page)+1, rowsPerPage, id);
      }) 
    } 
  }
  //funciones para manejar el paginado
  const cambiarPagina = async (event, newPage) => {
    setPage(newPage);
    await cargarImagenes(Number(newPage)+1, rowsPerPage, id);
  };

  const cambiarLimite = async (event) => {
    let limite = parseInt(event.target.value, 10);
    setRowsPerPage(limite);
    setPage(0);
    await cargarImagenes(1, limite, id)
  };

  return (
    <div>
      <Titulo>Lista de Imagenes</Titulo>
      <Button 
        component={Link}
        to={`/cms/imagen/crear/${id}`}
        params={{ id: id }}
        variant="contained" 
        startIcon={<AddCircle/>} 
        style={{marginBottom: '.5rem', backgroundColor: green[500], color: 'white'}}
      >
        Nuevo
      </Button>
      <TablaImagenes
        listaImagenes={listaImagenes}
        total={total}
        rowsPerPage={rowsPerPage}
        page={page}
        cambiarPagina={cambiarPagina}
        cambiarLimite={cambiarLimite}
        borrarImagen={borrarImagen}
      />
    </div>
  )
};

export default withRouter(ListaImagenes)
