import React, { useState } from 'react';
import { useHistory, withRouter } from 'react-router-dom';
import { TextField, MenuItem, Button } from '@material-ui/core';
import { red } from '@material-ui/core/colors';
import { DropzoneArea } from 'material-ui-dropzone';

import { postImagen } from '../../services/apiImagenes';

function CrearImagen({match}) {
  const history = useHistory();
  const [titulo, setTitulo] = useState('');
  const [tipo, setTipo] = useState('miniatura');
  const [file, setFile] = useState(null);
  const { id } = match.params;

  async function guardarImagen(e) {
    e.preventDefault();

    let formData = new FormData();

    if (id && file) {
      formData.append('titulo', titulo);
      formData.append('tipo', tipo);
      formData.append('novela', id);
      formData.append('file', file);

      await postImagen(formData).then(() => history.goBack());
    }
  }

  const cancelarForm = () => history.goBack();

  return (
    <form autoComplete="off" style={{ marginRight: '1rem' }} onSubmit={guardarImagen}>
      <div>
        <TextField 
          required 
          fullWidth
          autoFocus
          style={{ margin: 8 }}
          margin="dense"
          id="titulo" 
          label="Titulo"
          value={titulo}
          onChange={(e) => setTitulo(e.target.value)}
        />
        <TextField
          required
          style={{ margin: 8 }}
          margin="dense"
          id="tipo"
          select
          label="Tipo"
          value={tipo}
          onChange={(e) => setTipo(e.target.value)}
          helperText="Selecciona el tipo de imagen"
        >
          <MenuItem value="miniatura">Miniatura</MenuItem>
          <MenuItem value="portada">Portada</MenuItem>
          <MenuItem value="wallpaper">Wallpaper</MenuItem>
        </TextField>
        <DropzoneArea
          acceptedFiles={['image/*']}
          dropzoneText={"Arrastra y suela la imagen a subir"}
          onChange={(file) => setFile(file[0])}
          filesLimit={1}
        />
        <Button
          type="submit"
          variant="contained"
          color="primary"
          style={{ margin: '1rem' }}
        >Guardar</Button>
        <Button
          onClick={cancelarForm}
          variant="contained"
          style={{ color: 'white', backgroundColor: red[500] }}
        >Cancelar</Button>
      </div>
    </form>
  )
};

export default withRouter(CrearImagen)
