import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types';
import { 
  TextField, MenuItem, List, 
  ListItem, ListItemIcon, 
  Checkbox, ListItemText, Button 
} from '@material-ui/core';

import Etiquetas from './Etiquetas';
import { getExtras } from '../../services/apiExtras';

function FormularioNovela({novela={}, guardar}) {
  const [tipos, setTipos] = useState([]);
  const [categorias, setCategorias] = useState([]);
  const [etiquetas, setEtiquetas] = useState([]);
  const [objNovela, setObjNovela] = useState({
    titulo: '',
    titulo_alt: '',
    acron: '',
    autor: '',
    sinopsis: '',
    observacion: '',
    estado: 'emision',
    tipo: '',
    categorias: [],
    etiquetas: []
  });

  useEffect(() => {
    cargarExtras();
  }, [])

  useEffect(() => {
    if (Object.entries(novela).length > 0 ) {
      setObjNovela({...novela})
    }
  },[novela])
  //cargar los extras
  async function cargarExtras() {
    await getExtras().then(({data}) => {
      if (data.length > 0) {
        setTipos(data.filter(e => e.tipo === 1))
        setCategorias(data.filter(e => e.tipo === 2))
        setEtiquetas((data.filter(e => e.tipo === 3)).map(e => ({id:e.nombre,text:e.nombre})))
      }
    }).catch(error => console.log(error));
  }

  const seleccionarCategoria = (value) => () => {
    const currentIndex = (objNovela.categorias).indexOf(value);
    const newChecked = [...objNovela.categorias];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setObjNovela({...objNovela, categorias:[...newChecked]});
  };

  const guardarEtiquetas = (tags) => {
    if(tags.length>0) setObjNovela({...objNovela, etiquetas: tags.map(e => e.text)})
  };

  const guardarNovela = async (e) => {
    e.preventDefault();
    await guardar(objNovela);
  }

  return (
    <form autoComplete="off" style={{ marginRight: '1rem' }} onSubmit={guardarNovela}>
      <div>
        <TextField 
          required 
          fullWidth
          style={{ margin: 8 }}
          margin="dense"
          id="titulo" 
          label="Titulo"
          value={objNovela.titulo}
          onChange={(e) => setObjNovela({...objNovela, titulo: e.target.value})}
        />
        <TextField 
          required 
          fullWidth
          style={{ margin: 8 }}
          margin="dense"
          id="titulo_alt" 
          label="Titulo Alternativo" 
          value={objNovela.titulo_alt}
          onChange={(e) => setObjNovela({...objNovela, titulo_alt: e.target.value})}
        />
        <TextField 
          required
          style={{ margin: 8 }}
          margin="dense"
          id="acron" 
          label="Acrnimo"  
          value={objNovela.acron}
          onChange={(e) => setObjNovela({...objNovela, acron: e.target.value})}
        />
        <TextField 
          required
          style={{ margin: 8 }}
          margin="dense"
          id="autor" 
          fullWidth
          label="Autor" 
          value={objNovela.autor}
          onChange={(e) => setObjNovela({...objNovela, autor: e.target.value})}
        />
        <TextField
          required
          style={{ margin: 8 }}
          margin="dense"
          id="estado"
          select
          label="Estado"
          value={objNovela.estado}
          onChange={(e) => setObjNovela({...objNovela, estado: e.target.value})}
          helperText="Selecciona el estado de la novela"
        >
          <MenuItem value="emision">Emisin</MenuItem>
          <MenuItem value="finalizado">Finalizado</MenuItem>
          <MenuItem value="cancelado">Cancelado</MenuItem>
        </TextField>
        {/* TIPOS DE NOVELAS */}
        {tipos.length > 0 &&
          <TextField
            required
            style={{ margin: 8 }}
            margin="dense"
            id="tipo"
            select
            label="Tipo"
            value={objNovela.tipo}
            onChange={(e) => setObjNovela({...objNovela, tipo: e.target.value})}
            helperText="Selecciona el estado de la novela"
          >
            {
              tipos.map((e,i) => (
                <MenuItem key={i} value={e.nombre}>{e.nombre}</MenuItem>
              ))
            }
          </TextField>
        }<br/>
        {/* CATEGORIAS DE NOVELAS */}
        {categorias.length > 0 &&
          <List>
          {categorias.map((c, i) => (
            <ListItem key={i} dense button onClick={seleccionarCategoria(c.nombre)}>
              <ListItemIcon>
                <Checkbox
                  edge="start"
                  checked={(objNovela.categorias).indexOf(c.nombre) !== -1}
                  tabIndex={-1}
                  disableRipple
                  inputProps={{ 'aria-labelledby': c.nombre }}
                />
              </ListItemIcon>
              <ListItemText id={c.nombre} primary={c.nombre} />
            </ListItem>
          ))
            
          }
          </List>
        }
        {etiquetas.length > 0 &&
          <Etiquetas
            sugerencias={etiquetas}
            etiquetas={(objNovela.etiquetas).length>0?(objNovela.etiquetas).map(e => ({id:e,text:e})):objNovela.etiquetas}
            guardar={guardarEtiquetas}
          />
        }
        <TextField
          required
          style={{ margin: 8 }}
          margin="dense"
          id="sinopsis"
          label="Sinopsis"
          multiline
          fullWidth
          rows={5}
          placeholder="Sinopsis de la novela, usar etiqueta '<p></p>' para parrafos"
          variant="outlined"
          value={objNovela.sinopsis}
          onChange={(e) => setObjNovela({...objNovela, sinopsis: e.target.value})}
        />
        <TextField
          required
          style={{ margin: 8 }}
          margin="dense"
          id="observacion"
          label="Observacion"
          multiline
          fullWidth
          rows={2}
          placeholder="Observaciones"
          variant="outlined"
          value={objNovela.observacion}
          onChange={(e) => setObjNovela({...objNovela, observacion: e.target.value})}
        />
        <Button
          type="submit"
          variant="contained"
          color="primary"
          style={{ margin: '1rem' }}
        >Guardar</Button>
      </div>
    </form>
  )
};

FormularioNovela.propTypes = {
  novela: PropTypes.object,
  guardar: PropTypes.func
};

export default FormularioNovela;

