import React from 'react';
import clsx from 'clsx';
import { Drawer, Divider, IconButton, List, Typography } from '@material-ui/core';
import { ChevronLeft } from '@material-ui/icons';

import useStyles from './stylesLayout';
import { mainListItems } from './ListaMenu';

export default function LeftBar({cerrarMenu, open}) {
  const classes = useStyles();

  return (
    <Drawer
      variant="permanent"
      classes={{
        paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
      }}
      open={open}
    >
      <div className={classes.toolbarIcon}>
        <Typography component="h1" variant="h6" align="center" color="inherit" noWrap className={classes.title}>
          TNO CPanel
        </Typography>
        <IconButton onClick={cerrarMenu}>
          <ChevronLeft />
        </IconButton>
      </div>
      <Divider />
      <List>{mainListItems}</List>
      <Divider />
    </Drawer>
  )
}
