import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import moment from 'moment';
import { Button, TableContainer, Table, TableHead, TableRow, TableCell, TableBody, ButtonGroup, TableFooter, TablePagination } from '@material-ui/core';
import { red, cyan } from '@material-ui/core/colors';
import { Edit, Delete } from '@material-ui/icons';

import Dialogo from '../common/Dialogo';

function TablaCapitulos({
  listaCapitulos=[], 
  total=1, 
  rowsPerPage=30, 
  page=0, 
  cambiarPagina, 
  cambiarLimite, 
  borrarCapitulo}) {
  const [capituloTitulo, setCapituloTitulo] = useState('');
  const [idCapitulo, setIdCapitulo] = useState('');
  const [dialogoVisible, setDialogoVisible] = useState(false);

  async function eliminar() {
    await borrarCapitulo(idCapitulo);
    abrirDialogo();
  }
  //funcion para abrir el cuadro de eliminar
  const abrirDialogo = () => setDialogoVisible(!dialogoVisible);
  //funciones para manejar el cuadro de confirmacion
  const confirmarBorrado = (idCap, titulo) => {
    setCapituloTitulo(titulo)
    setIdCapitulo(idCap);
    abrirDialogo();
  }

  return (
    <div>
      <TableContainer style={{maxHeight: 500}}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell align="center">Capitulo</TableCell>
              <TableCell>Titulo</TableCell>
              <TableCell>Novela</TableCell>
              <TableCell align="center">Subido Por</TableCell>
              <TableCell align="center">Actualizado Por</TableCell>
              <TableCell align="center">Actualizado</TableCell>
              <TableCell align="center">Acciones</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
          {listaCapitulos.length > 0 ?
            listaCapitulos.map((capitulo, i) => (
              <TableRow key={i}>
                <TableCell align="center">{capitulo.numero}</TableCell>
                <TableCell>{capitulo.titulo}</TableCell>
                <TableCell>{capitulo.novela?capitulo.novela.titulo?capitulo.novela.titulo:'-':'-'}</TableCell>
                <TableCell align="center">{capitulo.subidoPor?capitulo.subidoPor.username:'-'}</TableCell>
                <TableCell align="center">{capitulo.actualizadoPor?capitulo.actualizadoPor.username:'-'}</TableCell>
                <TableCell align="center">{moment(capitulo.updatedAt).format('DD-MM-YYYY')}</TableCell>
                <TableCell>
                  <ButtonGroup size="small">
                    <Button component={Link} to={`/cms/capitulo/editar/${capitulo._id}`} params={{ id: capitulo._id }}>
                      <Edit style={{ color: cyan[500] }}/>   
                    </Button>
                    <Button onClick={() => confirmarBorrado(capitulo._id, capitulo.titulo)}>
                      <Delete style={{ color: red[500] }} />
                    </Button>
                  </ButtonGroup>
                </TableCell>
              </TableRow>
            ))
            :
            <TableRow style={{ height: 120 }}>
              <TableCell>Sin datos.</TableCell>
            </TableRow>
          }
          </TableBody>
        </Table>
      </TableContainer>
      <Table>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={[30, 50, 100]}
              colSpan={3}
              count={total}
              rowsPerPage={rowsPerPage}
              page={page}
              SelectProps={{
                inputProps: { 'aria-label': 'rows per page' },
                native: true,
              }}
              onChangePage={cambiarPagina}
              onChangeRowsPerPage={cambiarLimite}
            />
          </TableRow>
        </TableFooter>
      </Table>
      {/* DIALOGO PARA ELIMINAR CAPITULO */}
      <Dialogo
        titulo={`Eliminar '${capituloTitulo}'`}
        cuerpo="Esta accin no se puede deshacer."
        abrir={abrirDialogo}
        visible={dialogoVisible}
        accion={eliminar}
      />
    </div>
  )
}

TablaCapitulos.propTypes = {
  listaCapitulos: PropTypes.array,
  total: PropTypes.number,
  rowsPerPage: PropTypes.number,
  page: PropTypes.number,
  cambiarPagina: PropTypes.func,
  cambiarLimite: PropTypes.func,
  borrarCapitulo: PropTypes.func,
}

export default TablaCapitulos

